body {
  background: #001c42;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

a {
  color: #fff;
  text-transform: uppercase;
  display: block;
  text-align: center;
  margin-top: 24px;
  text-underline-offset: 4px;
  text-decoration-thickness: 2px;
  transition: opacity .15s ease;
}

.hamburger {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 200;
  height: 100px;
  width: 100px;
  transition: 0.3s;
}
@media (hover: hover) {
  .hamburger:hover {
    transform: translate(-50%, -50%) scale(1.5);
  }
  .hamburger:hover path {
    fill: #05b5f5;
  }
  .hamburger:hover .circle {
    display: none;
  }
}
.hamburger.menu-open circle {
  fill: transparent;
}
@media (hover: hover) {
  .hamburger.menu-open:hover {
    transform: translate(-50%, -50%) scale(1.5) rotate(90deg);
  }
}
